import {mapState} from 'vuex'
export default {
	data() {
		return {
			obj:'',
			url:'',
			id:'',
			src:''
		}
	},
	computed:{
		...mapState([
			'requesturl',
			'key'
		])
	},
	methods: {
		getdata:function(){
			var _this = this
			uni.request({
				url:_this.requesturl + _this.url + _this.key,
				success:function(data){
					console.log(data)
					_this.obj = data.data.newslist[0]
				}
			})
		},
		nextfn:function(){
			this.getdata()
		}
	},
	onLoad(options){
		console.log(options)
		var id = options.id;
		if(id == 2){
			this.id = 2
			this.url = '/txapi/hotreview/index?key='
			this.src = '../../static/img/wyy.png'
		}else if(id == 3){
			this.id = 3
			this.url = '/txapi/saylove/index?key='
			this.src = '../../static/img/qh.png'
		}else if(id == 4){
			this.id = 4
			this.url = '/txapi/caihongpi/index?key='
			this.src = '../../static/img/ch.png'
		}
		this.getdata()
	}
}
